.chat {
    border: 5px gray solid;
    height: 100vh;
}

.chat-content {
    position: relative;
    height: 83%;
    display: flex;
    flex-wrap: wrap;
}

.user-background {
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.messages-container {
    max-height: 75vh;
    overflow-y: scroll;
    width: 100%;
}

.messages {
    /* max-height: 75vh;
    overflow-y: scroll; */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
    width: 100%;
}

.empty-scroll {
    width: 100px;
    min-height: 50vh;
}

/* note */

.message {
    padding: 10px;
    display: inline-block;
    border: 3px solid gray;
    border-radius: 20px;
    min-width: 100px;
    max-width: 500px;
    background-color: gray;
}

.message:nth-child(n+2) {
    margin-top: 30px;
}

.message-header {
    color: rgba(113, 24, 12, 1);
    font-size: 23px;
    margin-bottom: 15px;
}

.message-content {
    font-size: 28px;
}

.mes-content-wrapper {
    width: 100%;
    max-width: 450px;
    display: flex;
    align-items: center;
}

.mes-video {
    display: block;
    width: 415px;
    padding-right: 10px;
}

.mes-audio {
    display: block;
    width: 350px;
    padding-right: 10px;
}

.mes-img {
    display: block;
    width: 415px;
    padding-right: 10px;
}

.mes-txt {
    word-wrap: break-word;
}

.message-fileload {
    display: flex;
    align-items: center;
}

.file-img {
    width: 50px;
    height: 50px;
    background-image: url('../../../img/files/file.png');
    background-size: cover;
    background-position: -10px 5px;
    background-repeat: no-repeat;
}

.fileload-img-link {
    width: 25px;
    height: 25px;
    background-size: cover;
    background-image: url('../../../img/files/download-arrow.png');
    align-self: flex-end;
}

.hidden {
    display: none;
}

.active {
    display: block;
}